.footer {
    background-color: black;
    padding: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 20px;
}

.footer .icons-section {
    display: flex;
    gap: 10px;
}

.footer .icons-section a {
    display: grid;
    place-content: center;
    transition: .5s;
}

.footer .icons-section a:hover {
    transform: scale(1.1);
}